*{
  margin:0;
  padding:0;
}

body{
  background-color:azure;
}
.my-app{
  width:50vw;
  height:100%;
  margin:auto;
  display:flex;
  align-items: center;
  flex-direction: column;
  box-shadow:1px 5px 5px 1px;
  padding:10px 0 10px 0;
  background-color: white;
}

.container{
    width:400px;
    border:1px solid black;
  }
  .calculator{
    display:grid;
    grid-template-columns:repeat(4,100px);
  }
  button.operator{
    background-color:lightgrey;
  }
  button{
    height:50px;
  }
  button:active{
    background-color: white;
  }
  .display{
    width:400px;
    text-align: right;
    font-size:50px;
    display:grid;
    grid-column-start:1;
    grid-column-end:5;
    
  }

  .txt{
   height:20px;
   text-align:left;
   font-size:20px;
  }